














import { createComponent } from '@vue/composition-api'
import playSound from '../lib/playSound'

export default createComponent({
  props: {
    score: { type: Number, default: 0 }
  },
  setup (props, ctx) {
    const start = () => {
      playSound('btn')
      ctx.emit('start')
    }
    return {
      start
    }
  }
})
